.plus-luck-notice {
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  padding-left: 1rem;
  padding-right: 1rem; }
  @media (max-width: 419px) {
    .plus-luck-notice {
      width: 100%;
      padding: 0.4rem; } }
  .plus-luck-notice .luck-title {
    border-bottom: 1px solid #dee2e6;
    color: #343a40;
    font-size: 1rem;
    padding-bottom: 0.2rem;
    margin-top: 0.2rem;
    margin-bottom: 0.4rem; }
  .plus-luck-notice .luck-list {
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
    padding-left: 1rem;
    padding-right: 1rem;
    display: grid;
    display: -ms-flexbox;
    flex-wrap: wrap;
    grid-gap: 0.4rem;
    grid-template-columns: repeat(6, 1fr); }
    @media (max-width: 419px) {
