.form {
  background: white;
  padding: 1.5rem;
  position: relative;
  padding-top: 2rem;
  width: 24rem;
  background-color: #7950f2; }
  .form .close {
    line-height: 2rem;
    font-size: 2rem;
    position: absolute;
    right: 0.4rem;
    top: 0rem;
    cursor: pointer; }
    .form .close:hover {
      color: #868e96; }
  .form .login-head {
    display: flex;
    display: -ms-flexbox;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center; }
    .form .login-head .login-img {
      width: 94px;
      height: 94px; }
      .form .login-head .login-img img {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.32), 0 3px 6px rgba(0, 0, 0, 0.46); }
    .form .login-head .title {
      font-size: 1.25rem;
      font-weight: 600;
      color: white; }
  .form .login-body {
    margin-top: 2rem;
    text-align: right; }
    .form .login-body img {
      cursor: pointer;
      width: 184px;
      height: 70px; }
  .form .description {
    margin-top: 0.25rem; }
